async function useDB_Connection(url, method = "GET", body = null, headers) {
  let response;
  try {
    response = await fetch(url, {
      method,
      body,
      headers,
    });
  } catch (err) {
    console.log("Connection error on connection hook: ", err);
    return false;
  }

  // receives data
  const rawData = await response.text();
  // processes to json
  let responseData;
  try {
    responseData = await JSON.parse(rawData);
  } catch (e) {
    console.log("Error on json parsing:\n", e);
  }

  if (!response.ok) {
    console.log("Error on response from API (error 25).");
    throw new Error(responseData.message);
  }
  // returns data processed
  if (responseData) {
    return responseData;
  } else {
    console.log("Error gathering data");
  }
}

export default useDB_Connection;
